import React from "react"
import {Container,Col,Row} from "react-bootstrap"
import Heading from "../Heading/Heading"
import data from "../../../jsons/products.json"
import Product from "../product/Product"
const Products = () => {
    
    return (
        <div className="products">
            <Container>
                <Heading title="Products" desc="Far far away, behind the word mountains, far from the countries Vokalia and Consonantia" />
                <Row>
                    {data.map((item,ind) => {
                        if(ind >3 && ind < 8){
                            return (
                                <Col key={ind} xs={12} md={6} lg={4} xl={3}>
                                    <Product  id={item.id} img={item.img} title={item.title} originPrice={item.originPrice} price={item.price} stars={item.stars} />

                                </Col>
                            )
                        }
                        return ""
                    })}
                </Row>

            </Container>
        </div>
    )
}



export default Products