import React from "react"
import {Container,Row,Col} from "react-bootstrap"

import data from "../../../jsons/bestSellers.json"

import Heading from "../Heading/Heading"
import Product from "../product/Product"
const BestSellers = () => {
    const returnProducts = () => {
        return (
            <>
                {data.map((item,i) => {
                    return (
                        <Col key={i} xs={12} md={6} lg={4} xl={3}>
                            <Product  id={item.id} img={item.img} title={item.title} originPrice={item.originPrice} price={item.price} stars={item.stars} />
                        </Col>
                    )
                })}
            </>
        )
    }
    return (
        <div className="best-sellers">
            <Container>
                <Heading title="Best Sellers" desc="Far far away, behind the word mountains, far from the countries Vokalia and Consonantia" />
                <Row>
                    {returnProducts()}
                </Row>
            </Container>
        </div>
    )
}

export default BestSellers